import * as actionType from './actionType'
import {getData} from 'util'
import {API_ENTRYORDERS_PAGE, API_ENTRYORDERS_ENTRYORDERTRACE_PAGE, API_ENTRYORDERS_CLOSEDORDERS, API_ENTRYORDERS_UPDATE, API_ENTRYORDERS_PRINT, API_ENTRYORDERS_EXPORT, API_ENTRYORDERS_BATCHDETAIL, API_ENTRYORDERS_PRINTTIMES, API_ENTRY_LOADCOUNTS, API_GET_MENUS_PERMISSIONS_BANS, API_ENTRYORDERS_ALLPRINT} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.INWAREHOUSELIST_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData, status) => (dispatch) => {
  dispatch({type: actionType.INWAREHOUSELIST_UPDATE, data: {tableLoading: true}})
  let reqObjSearchParams = {...reqData.searchParams, type: 'N'}
  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      i === 'productName' ? reqObjSearchParams[i] = reqObjSearchParams[i].children : reqObjSearchParams[i] = reqObjSearchParams[i].value
      // reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  if (reqObjSearchParams.initDate && reqObjSearchParams.initDate.length) {
    reqObjSearchParams.planReceiveDateStart = moment(reqObjSearchParams.initDate[0]).format('YYYY-MM-DD')
    reqObjSearchParams.planReceiveDateEnd = moment(reqObjSearchParams.initDate[1]).format('YYYY-MM-DD')
  }
  if (reqObjSearchParams.initDate1 && reqObjSearchParams.initDate1.length) {
    reqObjSearchParams.closeTimeStart = moment(reqObjSearchParams.initDate1[0]).format('YYYY-MM-DD')
    reqObjSearchParams.closeTimeEnd = moment(reqObjSearchParams.initDate1[1]).format('YYYY-MM-DD')
  }
  if (reqObjSearchParams.initDate2 && reqObjSearchParams.initDate2.length) {
    reqObjSearchParams.createTimeStart = moment(reqObjSearchParams.initDate2[0]).format('YYYY-MM-DD')
    reqObjSearchParams.createTimeEnd = moment(reqObjSearchParams.initDate2[1]).format('YYYY-MM-DD')
  }
  delete reqObjSearchParams.initDate
  delete reqObjSearchParams.initDate1
  delete reqObjSearchParams.initDate2
  reqData.searchParams = reqObjSearchParams

  if (status) {
    getData({api: API_ENTRYORDERS_PAGE, ...reqData}).then(data => {
      // 相应的逻辑判断
      let {page, size} = reqData.searchParams
      dispatch({
        type: actionType.INWAREHOUSELIST_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          selectedRowKeys: [], // 选中的队列key
          selectedRows: [], // 选中的队列数据
          searchResult: data.result.list || []
        }
      })
    }).catch(err => {
      dispatch({
        type: actionType.INWAREHOUSELIST_FILE
      })
    })
  } else {
    // 查询统计数量tabs
    getData({api: API_ENTRY_LOADCOUNTS, ...reqData}).then(({result}) => {
      console.log(result, 55)
      // 相应的逻辑判断
      let arr = ['0', '1', '2', '3']
      let status = ''
      let tabNumObj = {
        tab0: '',
        tab1: '',
        tab2: '',
        tab3: ''
      }
      result.map((o) => {
        console.log(o, 222)
        tabNumObj[`tab${o.key}`] = o.value
      })
      for (let i = 0; i < arr.length; i++) {
        for (let j = 0; j < result.length; j++) {
          if (result[j].key == arr[i] && result[j].value) {
            status = arr[i]
            break
          }
        }
        if (status) break
      }
      if (status) {
        reqData.searchParams.receiveStatus = status
      }
      getData({api: API_ENTRYORDERS_PAGE, ...reqData}).then(data => {
        // 相应的逻辑判断
        let {page, size} = reqData.searchParams
        dispatch({
          type: actionType.INWAREHOUSELIST_UPDATE,
          data: {
            page,
            size,
            tableLoading: false,
            total: data.result.total,
            tableActiveKey: status,
            selectedRowKeys: [], // 选中的队列key
            selectedRows: [], // 选中的队列数据
            searchResult: data.result.list || [],
            ...tabNumObj
          }
        })
      }).catch(err => {
        dispatch({
          type: actionType.INWAREHOUSELIST_FILE
        })
      })
    }).catch(err => {
      dispatch({
        type: actionType.INWAREHOUSELIST_FILE
      })
    })
  }
}

// 获取收货订单详情
export const handleGetDetail = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.INWAREHOUSELIST_UPDATE, data: {deliveryTableLoading: true}})
  getData({api: API_ENTRYORDERS_PAGE, ...reqData}).then(data => {
    callBack && callBack()
    // 相应的逻辑判断
    dispatch({
      type: actionType.INWAREHOUSELIST_UPDATE,
      data: {
        deliveryTableLoading: false,
        modalData: data.result || {}
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.INWAREHOUSELIST_FILE,
      data: {
        isShowDeliveryModal: false
      }
    })
    // failCallBack && failCallBack()
  })
}

