// 打印收货单
export const handleGetDetailPrint = (reqData, type) => (dispatch) => {
  dispatch({type: actionType.INWAREHOUSELIST_UPDATE, data: {printLoading: true}})
  let api = API_ENTRYORDERS_PRINT
  if (type && type == 1) {
    api = API_ENTRYORDERS_ALLPRINT
  }
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.INWAREHOUSELIST_UPDATE,
      data: {
        printLoading: false,
        printData: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.INWAREHOUSELIST_FILE
    })
  })
}

