import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Tabs
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const TabPane = Tabs.TabPane

@connect(
  state => ({...state.inWarehouseList, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '收货单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '生产日期',
      width: 150,
      dataIndex: 'produceDate',
      key: 'produceDate'
    }, {
      title: '单价（元）',
      width: 100,
      dataIndex: 'price',
      key: 'price'
    }, {
      title: '计划数',
      width: 100,
      dataIndex: 'planQty',
      key: 'planQty'
    }, {
      title: '发货数',
      width: 100,
      dataIndex: 'shippedQty',
      key: 'shippedQty'
    }, {
      title: '实收数',
      width: 100,
      dataIndex: 'receiveQty',
      key: 'receiveQty'
    }, {
      title: '实收赠品数',
      width: 100,
      dataIndex: 'giftQty',
      key: 'giftQty'
    }, {
      title: '上架数',
      width: 100,
      dataIndex: 'shelfQty',
      key: 'shelfQty'
    }, {
      title: '总额',
      width: 100,
      dataIndex: 'amount',
      key: 'amount'
    }, {
      title: '退货原因',
      width: 150,
      dataIndex: 'returnReason',
      key: 'returnReason'
    }, {
      title: '备注',
      width: 150,
      dataIndex: 'remark',
      key: 'remark'
    }]
    this.columnsT03 = [{
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '收货单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '生产日期',
      width: 150,
      dataIndex: 'produceDate',
      key: 'produceDate'
    }, {
      title: '单价（元）',
      width: 100,
      dataIndex: 'price',
      key: 'price'
    }, {
      title: '申偿数',
      width: 100,
      dataIndex: 'passQty',
      key: 'passQty'
    }, {
      title: '发货数',
      width: 100,
      dataIndex: 'shippedQty',
      key: 'shippedQty'
    }, {
      title: '实收数',
      width: 100,
      dataIndex: 'receiveQty',
      key: 'receiveQty'
    }, {
      title: '拒绝数',
      width: 100,
      dataIndex: 'rejectQty',
      key: 'rejectQty'
    }, {
      title: '总额',
      width: 100,
      dataIndex: 'amount',
      key: 'amount'
    }, {
      title: '退货原因',
      width: 150,
      dataIndex: 'returnReason',
      key: 'returnReason'
    }, {
      title: '备注',
      width: 150,
      dataIndex: 'remark',
      key: 'remark'
    }]
    this.columns2 = [{
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '生产日期',
      width: 150,
      dataIndex: 'produceDate',
      key: 'produceDate'
    }, {
      title: '单价（元）',
      width: 100,
      dataIndex: 'price',
      key: 'price'
    }, {
      title: '总金额（元）',
      width: 100,
      dataIndex: 'amount',
      key: 'amount'
    }, {
      title: '收货数',
      width: 100,
      dataIndex: 'receiveQty',
      key: 'receiveQty'
    }, {
      title: '上架储位',
      width: 150,
      dataIndex: 'shelfStoreLocationCode',
      key: 'shelfStoreLocationCode'
    }, {
      title: '上架数',
      width: 100,
      dataIndex: 'shelfQty',
      key: 'shelfQty'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleTabDetail, match: {params}} = this.props
    handleTabDetail({searchParams: {orderCode: params.id}})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {history} = this.props
    history.length > 1 ? history.goBack() : window.close()
  }
  render() {
    const {modalData, deliveryTableLoading, activeKey, update} = this.props
    return (
      <div className='fullOrderDel'>
        <span className='ant-modal-close-x close' onClick={this.handleCloseModal}></span>
        <Tabs activeKey={activeKey} onChange={(key) => {
          update({activeKey: key})
        }}>
          <TabPane tab={'入库单详情'} key="1">
            <div className='content'>
              <div className='header'>
                <Row type="flex" justify="start">
                  <Col span={6} className='delBox'>
                    <label>入库单号：</label>
                    <span>{modalData.orderCode || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>来源单号：</label>
                    <span>{modalData.originOrderCode || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>关联单号：</label>
                    <span>{modalData.linkOrderCode || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>供应商：</label>
                    <span>{modalData.supplierName || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>来源地点：</label>
                    <span>{`${modalData.sendLocationCode || ''}-${modalData.sendLocationName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>订单状态：</label>
                    <span>{modalData.receiveStatus || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>采购用途：</label>
                    <span>{modalData.orderSourceDesc || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>退货原因：</label>
                    <span>{modalData.returnReasonRemark || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>签收日期：</label>
                    <span>{modalData.signDate || ''}</span>
                  </Col>
                  {/* <Col span={6} className='delBox'>
                          <label>上架状态：</label>
                          <span>{modalData.shelfStatus || ''}</span>
                      </Col> */}
                </Row>
              </div>
              <div className='foot'>
                <div className='table-group'>
                  <Table
                    rowKey={record => record.id}
                    loading={deliveryTableLoading}
                    columns={modalData.entryType == 'T03' ? this.columnsT03 : this.columns}
                    scroll={{x: 'max-content', y: 400}}
                    dataSource={modalData.productItems}
                    pagination={false}
                  />
                </div>
              </div>
            </div>
          </TabPane>
          <TabPane tab={`入库明细（${modalData.batchCount}）`} key="2">
            {
              modalData.batchReponseList && modalData.batchReponseList.map((o, i) => <div className='table-group' key={o.batchCode}>
                <div className='header'>
                  <Row type="flex" justify="start">
                    <Col span={6} className='delBox'>
                      <label>入库批次：</label>
                      <span>{o.batchCode || ''}</span>
                    </Col>
                    <Col span={6} className='delBox'>
                      <label>创建时间：</label>
                      <span>{o.createTime || ''}</span>
                    </Col>
                    <Col span={6} className='delBox'>
                      <label>操作人：</label>
                      <span>{o.createBy || ''}</span>
                    </Col>
                    <Col span={6} className='delBox'>
                      <label>是否冲销：</label>
                      <span>{o.writeOffFlag ? '已冲销' : '未冲销'}</span>
                    </Col>
                  </Row>
                </div>
                <Table
                  rowKey={record => Math.random()}
                  loading={deliveryTableLoading}
                  columns={this.columns2}
                  scroll={{x: 'max-content', y: 400}}
                  dataSource={o.batchDetailList}
                  pagination={false}
                />
              </div>)
            }
          </TabPane>
        </Tabs>
      </div>
    )
  }
}



